Spin.prototype.setSleepTimer = function (minutes) {
  if (this.bleDevice) this.bleDevice.setSleepTimer(minutes);else {
    var ms = minutes * 60 * 1000;
    this.sendCommand(this.id, 'SLEEPTIMER', ms);
  }
};

Spin.prototype.destroy = function () {
  this.disconnect();
  this.log('destroying');

  this._removeEvents();

  delete this.state;
};

